import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data, activePanel: "home"});
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	share = () => {
	var x = connect.send("VKWebAppCallAPIMethod", {"method": "group.isMember", "params": {"group_id": 173243972, "user_ids": this.fetchedUser.id, "v":"5.95", "access_token":"79d23ccd6697b3fd1b4bf95bba7ac60c47a9b96610e6d7bc5949a3e87cd7da21caca864a46afd1ef7a5d9"}}); 
	console.log(x);
	if(!x.member) { 
		connect.send("VKWebAppJoinGroup", {"group_id": 173243972}); }
	else { }
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} share={this.share}/>
				<Persik id="persik" go={this.go} />
			</View>
		);
	}
}

export default App;
